body {
  margin: 0;
}

.container {
  display: flex;
  flex-wrap: wrap;
  align-content: stretch;
}

.block {
  height: 300px;
  flex-basis: 33.33%;
  min-width: 300px;
  color: #fff;
  line-height: 300px;
  font-family: Arial, sans-serif;
  font-size: 100px;
  text-align: center;
}

.block-1 {
  background-color: rgb(255, 0, 0);
}

.block-2 {
  background-color: rgb(204, 0, 0);
}

.block-3 {
  background-color: rgb(153, 0, 0);
}

.block-4 {
  background-color: rgb(102, 0, 0);
  order: 6;
}

.block-5 {
  background-color: rgb(51, 0, 0);
  order: 5;
}

.block-6 {
  background-color: rgb(0, 0, 0);
  order: 4;
}

@media (max-width: 899px) {
  .block {
    flex-basis: 50%;
  }

  .block-3 {
    order: 4;
  }

  .block-4 {
    order: 3;
  }

  .block-5 {
    order: 5;
  }
  
  .block-6 {
    order: 6;
  }
}

@media (max-width: 599px) {
  .block {
    flex-basis: 100%;
  }

  .block-3 {
    order: 3;
  }

  .block-4 {
    order: 4;
  }
}
